"use client";
import React, { useState, useEffect } from "react";
import { useTasks } from "../hooks/useTasks";
import { Task } from "../interfaces/task";
import { Trash } from "lucide-react";

interface TaskListProps {
  status: boolean;
  title: string;
}

export const TaskList: React.FC<TaskListProps> = ({ status, title }) => {
  const { tasks, fetchTasks, updateTask, deleteTask, setTasks } = useTasks();
  const [draggedIndex, setDraggedIndex] = useState<number | null>(null);
  const [hoveredId, setHoveredId] = useState<string | null>(null);

  const filteredTasks = tasks
    .filter((task) => task.status === status)
    .sort((a, b) => (a.index_position ?? 0) - (b.index_position ?? 0));

  useEffect(() => {
    fetchTasks();
    // eslint-disable-next-line
  }, []);

  // Drag and drop handlers
  const handleDragStart = (index: number) => setDraggedIndex(index);
  const handleDragOver = (e: React.DragEvent<HTMLLIElement>, index: number) => {
    e.preventDefault();
    if (draggedIndex === null || draggedIndex === index) return;
    const updated = [...filteredTasks];
    const [removed] = updated.splice(draggedIndex, 1);
    updated.splice(index, 0, removed);
    // Atualiza index_position localmente
    updated.forEach((task, idx) => (task.index_position = idx));
    setTasks((prev) => {
      // Atualiza apenas as tasks deste tipo
      const others = prev.filter((t) =>
        status === true ? t.status === false : t.status === true
      );
      return [...others, ...updated];
    });
    setDraggedIndex(index);
  };
  const handleDragEnd = async () => {
    setDraggedIndex(null);
    // Atualiza ordem no backend
    await fetchTasks(); // Garante tasks atualizadas
    const toUpdate = filteredTasks.map((task, idx) => ({
      id: task.id,
      index_position: idx,
    }));
    await fetch("/api/tasks/reorder", {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ tasks: toUpdate }),
    });
    fetchTasks();
  };

  // Atualiza status da task
  const handleToggle = async (task: Task) => {
    const newStatus = !task.status;
    await updateTask(task.id, { status: newStatus });
    fetchTasks();
  };

  // Deleta task
  const handleDelete = async (id: string) => {
    await deleteTask(id);
    fetchTasks();
  };

  return (
    <div
      className={`bg-white rounded-xl shadow-md p-6 w-full max-w-xs border-t-8 ${
        status === true ? "border-orange-400" : "border-green-500"
      }`}
    >
      <h2 className="text-2xl font-bold text-black mb-1 text-center">
        {title}
      </h2>

      <ul className="flex flex-col gap-2 mb-6">
        {filteredTasks.map((task, idx) => (
          <li
            key={task.id}
            className={`flex items-center gap-2 px-2 py-1 rounded transition-colors group ${
              status === true ? "hover:bg-orange-50" : "hover:bg-green-50"
            }`}
            draggable
            onDragStart={() => handleDragStart(idx)}
            onDragOver={(e) => handleDragOver(e, idx)}
            onDragEnd={handleDragEnd}
            onMouseEnter={() => setHoveredId(task.id)}
            onMouseLeave={() => setHoveredId(null)}
          >
            <input
              type="checkbox"
              checked={task.status === true}
              onChange={() => handleToggle(task)}
              className={`accent-${
                status === true ? "orange-400" : "green-500"
              } w-5 h-5`}
            />
            <span
              className={`flex-1 text-base ${
                task.status === true
                  ? "line-through text-gray-400"
                  : "text-black"
              }`}
            >
              {task.title}
            </span>
            {hoveredId === task.id && (
              <button
                className="text-xs text-gray-400 hover:text-red-500 ml-2 hover:cursor-pointer"
                onClick={() => handleDelete(task.id)}
              >
                <Trash />
              </button>
            )}
          </li>
        ))}
      </ul>
      <button className="w-full py-2 rounded bg-black text-white font-semibold hover:bg-gray-800 transition hover:cursor-pointer">
        ERASE ALL
      </button>
    </div>
  );
};

export default TaskList;
