"use client";
import { useState } from "react";
import api from "./axios-config";
import { Task, TaskResponse } from "../interfaces/task";

export function useTasks() {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  async function fetchTasks() {
    setLoading(true);
    setError(null);
    try {
      const res = await api.get<TaskResponse>("/tasks");
      setTasks(res.data.tasks);
    } catch (e: any) {
      setError(e.response?.data?.message || "Erro ao buscar tasks");
    } finally {
      setLoading(false);
    }
  }

  async function fetchTaskById(id: string): Promise<Task | null> {
    setLoading(true);
    setError(null);
    try {
      const res = await api.get<Task>(`/tasks/${id}`);
      return res.data;
    } catch (e: any) {
      setError(e.response?.data?.message || "Erro ao buscar task");
      return null;
    } finally {
      setLoading(false);
    }
  }

  async function createTask(
    task: Omit<Task, "id" | "createdAt" | "updatedAt" | "userId">
  ): Promise<Task | null> {
    setLoading(true);
    setError(null);
    try {
      const res = await api.post<Task>("/tasks", task);
      setTasks((prev) => [...prev, res.data]);
      return res.data;
    } catch (e: any) {
      setError(e.response?.data?.message || "Erro ao criar task");
      return null;
    } finally {
      setLoading(false);
    }
  }

  async function updateTask(
    id: string,
    updates: Partial<Omit<Task, "id" | "userId" | "createdAt">>
  ): Promise<Task | null> {
    setLoading(true);
    setError(null);
    try {
      const res = await api.put<Task>(`/tasks/${id}`, updates);
      setTasks((prev) =>
        prev.map((task) => (task.id === id ? res.data : task))
      );
      return res.data;
    } catch (e: any) {
      setError(e.response?.data?.message || "Erro ao atualizar task");
      return null;
    } finally {
      setLoading(false);
    }
  }

  async function deleteTask(id: string): Promise<boolean> {
    setLoading(true);
    setError(null);
    try {
      await api.delete(`/tasks/${id}`);
      setTasks((prev) => prev.filter((task) => task.id !== id));
      return true;
    } catch (e: any) {
      setError(e.response?.data?.message || "Erro ao deletar task");
      return false;
    } finally {
      setLoading(false);
    }
  }

  return {
    tasks,
    loading,
    error,
    fetchTasks,
    fetchTaskById,
    createTask,
    updateTask,
    deleteTask,
    setTasks,
  };
}
